@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i&display=swap);
* {
  margin: 0;
  padding: 0;
  outline: 0;
  box-sizing: border-box; }
  * html,
  * body,
  * #root {
    background-color: #2E5E80;
    height: 100%;
    font-family: 'Open Sans', sans-serif; }
  * #root {
    display: flex; }

.left-content {
  background-color: #5CBBFF;
  color: #2E5E80;
  width: 300px;
  padding: 15px;
  display: flex;
  align-items: center;
  flex-direction: column; }

.right-content {
  width: 100%;
  color: #5CBBFF; }
  .right-content div[class^=container-] {
    padding: 0 15px; }
  .right-content .titulo {
    width: 100%;
    margin: 20px 0;
    border-bottom: 2px solid;
    text-align: center;
    font-weight: 400;
    font-size: 40px; }

@media (max-width: 768px) {
  .left-content {
    margin-left: -300px; } }

.container {
  position: fixed;
  width: 200px;
  height: 70vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between; }
  .container h6 {
    font-size: 1.1em;
    font-weight: 600;
    border-bottom: 1px solid;
    margin-bottom: 10px; }
  .container .side-hidden {
    display: none;
    margin-left: 250px;
    background-color: gray;
    width: 30px;
    height: 30px;
    color: white;
    cursor: pointer; }
  @media (max-width: 768px) {
    .container .side-hidden {
      display: inline;
      display: initial; } }
  .container .img-profile {
    background-color: #2E5E80;
    width: 200px;
    height: 200px;
    border-radius: 100px;
    overflow: hidden; }

.container-habilidades .items {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap; }
  .container-habilidades .items .item {
    padding: 15px; }
    .container-habilidades .items .item svg {
      margin: 0 5px; }

.container-projetos .projetos {
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap; }
  .container-projetos .projetos .projeto {
    width: 310px;
    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 15px; }
    .container-projetos .projetos .projeto .img-container {
      width: 280px;
      height: 100%;
      border-radius: 10px;
      padding: 0;
      margin: 0;
      overflow: hidden;
      border: none; }
      .container-projetos .projetos .projeto .img-container img {
        width: 100%;
        height: 100%; }
    .container-projetos .projetos .projeto .img-container:hover {
      z-index: 1;
      box-shadow: 0 0 10px 0 #545454; }
    .container-projetos .projetos .projeto > div {
      flex-grow: 1;
      border: 1px solid;
      padding: 5px;
      border-radius: 10px;
      margin-top: -100px;
      background-color: #2E5E80; }

